.cb-wrapper-carousel {
  overflow: hidden;
  padding: 0 rem(20) rem(20);
  height: calc(100% - 80px);
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media (min-width: 768px) {
    padding: 0 rem(40) rem(40);
    height: calc(100% - 116px);
  }

  @media (min-width: 992px) {
    padding: 0 rem(40) rem(75);
  }
}

.cb-carousel{
  display: flex;
  align-items: flex-end;

  @media (min-width: 768px) {
    height: 100%;
  }
}

.cb-carousel .swiper-wrapper {
  display: flex;
  align-items: flex-end;
  height: 100%;
  transition-duration: .002s!important;
}

.cb-card {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 548px;

  @media (min-width: 768px) {
    width: auto;
  }
  
  img {

    @media (min-width: 768px) {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  &__trigger {
    width: 20px;
    height: 20px;
    position: relative;
    z-index: 2;
    background-color: $brand-secondary;
    border-radius: 50%;
    cursor: pointer;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .play {
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 20px 0 20px 30px;
    border-color: transparent transparent transparent $brand-secondary;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
